body {
  /* margin: 0; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width:  100vw;
  background: linear-gradient(45deg,  #9979C1,#F07BBB);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#header{
  margin: 250px;
}

.h1{
  font-size: 35px;
  text-align: center;
  border-radius: 3px;
  padding: 20px 40px;
  width: 250px;
  margin-bottom: 25px;
  border: 2px solid aliceblue;
  color: aliceblue;
}

.square{
  cursor: pointer;
  width: 80px;
  height: 80px;
  border: 2px solid black;
  margin: 3px;
  float: left;
  background-color: #FF8EA3;
  color: black;
  font-size: 30px;
  font-weight: bold;
}

#board{
  /* border: 10px solid black; */
  height: 280px;
  margin-left: 36px;
}

.square:hover{
  border: 2px solid aliceblue;
  color: #fff;
}

.footer{
  /* border: 1px solid black; */
  width: 333px;
  text-align: center;
}

.btn{
  margin: 20px 40px;
  padding: 10px 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 24px;
  background-color: #e47cb8;
  color:aliceblue;
}


